import React, {Component} from 'react';
import './artist.css'

class Artist extends Component{
    constructor(props){
       super(props);
       this.state = {
           countLikes: 0,
       }
       this.nameMusic=props.nameMusic;
       this.photoMusic=props.photoMusic;
       this.play= props.play;
       this.keySongs=props.keySongs;
       this.handleCount=this.handleCount.bind(this);
       this.handleCountDesc=this.handleCountDesc.bind(this)
    }

handleCount(){
  this.setState({countLikes: this.state.countLikes+1})
}

handleCountDesc(){
    this.setState({countLikes: this.state.countLikes-1})
}

    render(){
        return(          
            <div className="musics">
            <h5>{this.nameMusic}</h5>
            <img src={this.photoMusic}/><br/>
            <span onClick={this.handleCount} className="fas fa-heart fa-1x like" ></span> <h4>{this.state.countLikes}</h4><span onClick={this.handleCountDesc} className="fas fa-thumbs-down fa-1x dislike"></span>
            <label>  Reproducciones  </label> {this.play}    
            </div>
        )

    }
}

export default Artist;